/** add a "v-align-center" class on container (used on page header) **/
.v-align-center.container {
  padding-top: 30px;
  padding-bottom: 30px;
}
@media (min-width: 480px) {
  .v-align-center.container {
    display: table;
    height: 100%;
    position: relative;
  }
  .v-align-center.container > .row {
    display: table-row;
    height: 100%;
    float: none;
    padding: 0;
  }
  .v-align-center.container > .row > div[class*='col-'] {
    display: table-cell;
    vertical-align: middle;
    float: none;
    padding: 0;
  }
}
.v-align-center-transform {
  position: relative;
}
.v-align-center-transform .container {
  position: absolute;
  height: 100%;
  z-index: 1000;
  width: 100%;
}
.v-align-center-transform .container > .row {
  position: absolute;
  height: auto;
  width: 100%;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
