*/
.feature-box {
  text-align: center;
  display: block;
  /* center the icon or image on mobile */ }

.feature-box:before,
.feature-box:after {
  content: " ";
  display: table; }

.feature-box:after {
  clear: both; }

.feature-box.media-left,
.feature-box.media-right {
  padding: 0; }

.feature-box i[class^="icon-"],
.feature-box i[class*=" icon-"],
.feature-box img,
.feature-box .icon {
  margin: 0 0 15px 0; }

.feature-box i[class^="icon-"].rounded,
.feature-box i[class*=" icon-"].rounded,
.feature-box img.rounded,
.feature-box .icon.rounded,
.feature-box i[class^="icon-"].squared,
.feature-box i[class*=" icon-"].squared,
.feature-box img.squared,
.feature-box .icon.squared,
.feature-box i[class^="icon-"].circle,
.feature-box i[class*=" icon-"].circle,
.feature-box img.circle,
.feature-box .icon.circle {
  margin: 0 0 15px 0; }

.feature-box .feature-box-content {
  margin-bottom: 60px;
  overflow: hidden; }

.feature-box .feature-box-content p:last-child,
.feature-box .feature-box-content .btn:last-child,
.feature-box .feature-box-content .comment-form input[type="submit"]:last-child, .comment-form
.feature-box .feature-box-content input[type="submit"]:last-child,
.feature-box .feature-box-content .search-form .search-submit:last-child, .search-form
.feature-box .feature-box-content .search-submit:last-child {
  margin-bottom: 0; }

