/**
* ICON BOX
* ==================================================
*/
.box-icon {
  text-align: center;
  padding: 30px 30px 0 30px; }

.box-icon:before,
.box-icon:after {
  content: " ";
  display: table; }

.box-icon:after {
  clear: both; }

.box-icon.main-color,
.box-icon.dark-main-color,
.box-icon.light-main-color,
.box-icon.dark-color,
.box-icon.light-color {
  padding-bottom: 30px; }

.box-icon i {
  margin-bottom: 30px;
  display: inline-block; }

.box-icon i:before {
  margin-right: 0; }

.box-icon p,
.box-icon .btn,
.box-icon .comment-form input[type="submit"], .comment-form
.box-icon input[type="submit"],
.box-icon .search-form .search-submit, .search-form
.box-icon .search-submit {
  margin-bottom: 0; }

.box-icon a h1,
.box-icon a h2,
.box-icon a h3,
.box-icon a h4,
.box-icon a h5,
.box-icon a h6 {
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  -ms-transition: all 0.2s;
  transition: all 0.2s; }

.box-icon a h1:nth-child(2),
.box-icon a h2:nth-child(2),
.box-icon a h3:nth-child(2),
.box-icon a h4:nth-child(2),
.box-icon a h5:nth-child(2),
.box-icon a h6:nth-child(2) {
  margin-top: 30px; }

.box-icon a p {
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  -ms-transition: all 0.2s;
  transition: all 0.2s; }

/*link*/
.box-icon a i {
  display: inline-block; }

.box-icon a:hover,
.box-icon a:focus {
  text-decoration: none; }

/*animate icon*/
.box-icon.animated a:hover i:before {
  -moz-transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
  -ms--transform: rotate(360deg);
  transform: rotate(360deg);
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  -ms-transition: all 0.2s;
  transition: all 0.2s; }

